import Vue from 'vue';
import App from './App.vue';

Vue.config.productionTip = false;
Vue.config.devtools = true;

if (process.env.MIX_APP_ENV === 'production') {
  Vue.config.devtools = true;
  Vue.config.debug = true;
  Vue.config.silent = false;
}

new Vue({
  render: (h) => h(App),
}).$mount('#app');
